<template>
  <div>
    <div id="nav">
      <ul>
        <router-link tag="li" to="/" active-class="active" exact class="lg-menu" >
          <a>HOME</a>
        </router-link>

        <router-link tag="li" to="/projects" active-class="active" class="lg-menu" >
          <a>PROJECTS</a>
        </router-link>

        <router-link tag="li" to="/contact" active-class="active" class="lg-menu" >
          <a>CONTACT</a>
        </router-link>
        <li id="menu-icon" ref="menuIcon" @click="displayMenu">
           &#9776;
        </li>
      </ul>
    </div>
    <div class="sm-menu" ref="smMenu">
      <div @click="shrinkMenu">
        <router-link to="/" active-class="active" exact >
          <a>HOME</a>
        </router-link>
      </div>
      <div @click="shrinkMenu">
        <router-link to="/projects" active-class="active" >
          <a>PROJECTS</a>
        </router-link>
      </div>
      <div @click="shrinkMenu">
        <router-link to="/contact" active-class="active" >
          <a>CONTACT</a>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    displayMenu () {
      if (this.$refs.smMenu.style.display === 'block') {
        this.$refs.smMenu.style.display = 'none'
        this.$refs.menuIcon.innerHTML = '&#9776;'
      } else {
        this.$refs.smMenu.style.display = 'block'
        this.$refs.menuIcon.innerHTML = 'X'
      }
    },
    hideMenu () {
      if (window.innerWidth > 754) {
        this.$refs.smMenu.style.display = 'none'
      }
    },
    shrinkMenu () {
      this.$refs.smMenu.style.display = 'none'
      this.$refs.menuIcon.innerHTML = '&#9776;'
    }
  },
  mounted () {
    window.addEventListener('resize', this.hideMenu)
  }
}
</script>

<style scoped lang="scss">
  * {
    box-sizing: border-box;
  }
  body {
    font-size: 1.6rem;
  }
  #nav {
    background-color: #fff;
    padding: 5px;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
  }
  ul li {
    list-style-type: none;
    font-size: 1.6rem;
  }
  ul {
    display: flex;
    justify-content: flex-end;
  }
  ul li {
    margin-right: 50px;
  }
  ul li a {
    text-decoration: none;
    color: #2C3135;
  }
  .active {
    border-bottom: 2px solid #2C3135;
  }
  #menu-icon {
   font-size: 3rem;
   display: none;
  }
  .sm-menu {
    display: none;
    background-color: #fff;
    color: #2C3135;
    position: absolute;
    text-align: center;
    font-size: 1.6rem;
    top: 300;
    animation: animateMenu 200ms ease-out forwards;
  }
  .sm-menu > div {
    padding: 15px 0px;
  }
  .sm-menu a {
    text-decoration: none;
    color: #2C3135;
  }
  @media (max-width: 754px) {
    #menu-icon {
      display: block;
      cursor: pointer;
      outline: none;
    }
    .lg-menu {
      display: none;
    }
  }
  @keyframes animateMenu {
    from {
     width: 0%;
    }
    to {
      width: 100%;
    }
  }
</style>
